	module.exports = {"data":{"wordpressPost":{"title":"Editors’ Picks 2015: Part Five","content":"<h2><a href=\"http://bmjopen.bmj.com/content/5/11/e009051.full\"><strong>How well reported are interventions in systematic reviews?</strong></a></h2>\n<p><a href=\"http://blogs.bmj.com/bmjopen/files/2016/01/P5.jpg\" rel=\"attachment wp-att-934\"><img class=\"alignnone  wp-image-934\" src=\"http://blogs.bmj.com/bmjopen/files/2016/01/P5-300x114.jpg\" alt=\"P5\" width=\"537\" height=\"204\" srcset=\"http://blogs.bmj.com/bmjopen/files/2016/01/P5-300x114.jpg 300w, http://blogs.bmj.com/bmjopen/files/2016/01/P5-768x291.jpg 768w, http://blogs.bmj.com/bmjopen/files/2016/01/P5-1024x388.jpg 1024w, http://blogs.bmj.com/bmjopen/files/2016/01/P5.jpg 1280w\" sizes=\"(max-width: 537px) 100vw, 537px\" /></a></p>\n<p><strong>In December we surveyed our staff editors to tell us about their favourite articles from 2015. Part 5 focuses on another important editorial issue: the quality of reporting in systematic reviews.</strong></p>\n<p>Clinicians and healthcare professionals often rely on systematic reviews and meta-analyses for information on how effective interventions are, as they do not have the time to read through the literature on each individual trial that is published. But how useful are systematic reviews for clinicians and are the interventions reported in sufficient detail for clinicians to implement them in clinical practice? Interventions should also be reported in sufficient detail so that other researchers could independently replicate the interventions in future studies.</p>\n<p>In our <a href=\"http://bmjopen.bmj.com/content/5/11/e009051.full\">fifth editors&#8217; pick</a>, <a href=\"http://apps.bond.edu.au/staff/profile.asp?s_id=3861\">Tammy Hoffman</a> and colleagues selected a random sample of systematic reviews of non-pharmacological interventions for stroke, and examined the completeness of reporting of the interventions in the reviews. Using an established reporting instrument called the Template for Intervention Description and Replication (TIDieR) checklist, the authors found that a number of checklist items were incompletely reported in the reviews, including: (1) modifications to the intervention; (2) a description of intervention adherence/ fidelity; (3) a description of the materials used in the intervention; (4) a description of the procedures used in the intervention and; (5) if there was a plan for the intervention to be personalised or adapted during the study. The incomplete descriptions of the intervention materials and procedures were highlighted as crucial missing elements that have previously been reported as most frequently missing in publications of individual randomized trials.</p>\n<p>The authors concluded that better reporting of interventions were needed in systematic reviews if they were going to be effectively used and interpreted by readers. Recommendations are provided for authors of systematic reviews to use the TIDieR checklist as a guide for reporting interventions, and to provide an ‘intervention options table’ that summarises the pros and cons of all usable and feasible interventions reported in the review.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"be080966-c8aa-5c09-96a0-ae5ddafc1781"}}

